import {agentLookup} from '../../agents';
import {abilityIconSize, colorAttackers, colorDefenders} from '../../constants';

function getDistancedPoint(pt1, pt2, length) {
  let adj = pt2[0] - pt1[0];
  let opp = pt2[1] - pt1[1];
  let angle = Math.atan(opp/adj);
  let newOpp = Math.sin(angle) * length;
  let newAdj = Math.cos(angle) * length;
  pt2 = [pt1[0] + newAdj, pt1[1] + newOpp];
  if (adj < 0) {
    pt2 = [pt1[0] - newAdj, pt1[1] - newOpp];
  }
  return pt2;
}

export function addPoint(context, x, y, size, color) {
  const offset = size > 1 ? size / 2 : 0;
  // addRect(context, x - offset, y - offset, size, size, color);
  console.log(`${x - offset}, ${y - offset} ${size} ${color}`)
  context.beginPath();
  context.lineCap = "square";
  context.rect(x - offset, y - offset, size, size);
  context.strokeStyle = color;
  context.lineWidth = size;
  context.stroke();
}

export function addPath(context, path, size, color) {
  context.beginPath();
  context.lineWidth = size;
  context.lineJoin = 'round';
  context.strokeStyle = color;
  path.forEach((pt, i) => {
    if (i === 0) {
      context.moveTo(...pt);
      return;
    }
    context.lineTo(...pt);
  })
  context.stroke();
  return {
    tool: 'pencil',
    path,
    size,
    color
  };
}

// todo need to handle negative angle (going NW or SW
export function addLine(context, pt1, pt2, size, color, length, icon) {

  if (length) {
    // let adj = pt2[0] - pt1[0];
    // let opp = pt2[1] - pt1[1];
    // let angle = Math.atan(opp/adj);
    // let newOpp = Math.sin(angle) * length;
    // let newAdj = Math.cos(angle) * length;
    // pt2 = [pt1[0] + newAdj, pt1[1] + newOpp];
    // if (adj < 0) {
    //   pt2 = [pt1[0] - newAdj, pt1[1] - newOpp];
    // }
    pt2 = getDistancedPoint(pt1, pt2, length);
  }

  context.beginPath();
  context.moveTo(...pt1);
  context.lineTo(...pt2);
  context.lineWidth = size;
  context.strokeStyle = color;
  context.stroke();

  if (icon) {
    const iconPoint = getDistancedPoint(pt1, pt2, length / 2);
    console.log('icon pt', iconPoint, icon)
    // addImage(context, iconPoint[0], iconPoint[1], abilityIconSize, abilityIconSize, icon);
    addAbilityIcon(context, iconPoint[0], iconPoint[1], icon);
  }
}

export function addArrow(context, fromx, fromy, tox, toy, color) {
  let headlen = 10; // length of head in pixels
  let dx = tox - fromx;
  let dy = toy - fromy;
  let angle = Math.atan2(dy, dx);
  context.beginPath();
  context.moveTo(fromx, fromy);
  context.lineTo(tox, toy);
  context.lineTo(tox - headlen * Math.cos(angle - Math.PI / 6), toy - headlen * Math.sin(angle - Math.PI / 6));
  context.moveTo(tox, toy);
  context.lineTo(tox - headlen * Math.cos(angle + Math.PI / 6), toy - headlen * Math.sin(angle + Math.PI / 6));
  context.strokeStyle = color;
  context.lineWidth = 2;
  context.stroke();
  return {
    tool: 'arrow',
    start: [fromx, fromy],
    end: [tox, toy],
    color
  }
}

export function addText(context, x, y, size, text, color = 'black') {
  context.beginPath();
  context.fillStyle = color;
  context.textAlign = 'left';
  context.font = size + 'px arial';
  context.fillText(text, x, y);
  context.stroke();
  return {
    tool: 'text',
    center: [x, y],
    size,
    text,
    color
  }
}

export function addCircle(context, x, y, radius, text, bgcolor, stroke) {
  context.beginPath();
  context.arc(x, y, radius, 0, 2 * Math.PI);
  if (bgcolor) {
    context.fillStyle = bgcolor;
    context.fill();
  }
  if (text) {
    context.fillStyle = 'black';
    context.textAlign = 'center';
    context.fillText(text, x, y + 3);
  }
  if (stroke) {
    context.strokeStyle = stroke;
  } else {
    context.strokeStyle = bgcolor;
  }
  context.lineWidth = 2;
  context.stroke();
  return {
    tool: 'circle',
    center: [x, y],
    size: radius,
    text,
    color: bgcolor,
    stroke
  }
}

export function addSpike(context, x, y) {
  addImage(context, x, y, 30, 30, require('../../img/spike.png'));
  return {
    tool: 'spike',
    center: [x, y]
  }
}

function addImage(context, x, y, w, h, src) {
  let img = new Image(w, h);
  img.src = src;
  img.addEventListener('load', () => {
    context.beginPath();
    context.drawImage(img, x - (w / 2), y - (h / 2), w, h);
    context.stroke();
  });
}

function addRect(context, x, y, w, h, color) {
  context.beginPath();
  context.rect(x, y, w, h);
  context.strokeStyle = color;
  context.lineWidth = 2;
  context.stroke();
}

// change this to add agent or their abilities
export function addAgentItem(item, context, x, y, agent, side, opts) {
  const agentObj = agentLookup[agent];
  const teamColor = side === 'a' ? colorAttackers : colorDefenders;

  if (item === 'icon') {
    addCircle(context, x, y - 3, 18, null, teamColor);
    addImage(context, x, y, 30, 30, agentObj.icon);
    // addRect(context, x - 17, y - 17, 34, 34, teamColor);
  } else {
    const abilityObj = agentObj.abilities.find(a => a.name === item);
    // add option for team color backgrounds (rather than ability colors)
    if (abilityObj.tool === 'circle') {
      // TODO: if only stroke, have stroke use team color rather than setting as fill
      // TODO: option to show all icons?
      let adjustedColor = opts.useTeamColor && abilityObj.color ? teamColor : abilityObj.color;
      let adjustedStroke = opts.useTeamColor && abilityObj.stroke ? teamColor : abilityObj.stroke;
      addCircle(context, x, y, abilityObj.radius, '', adjustedColor, adjustedStroke);
      if (abilityObj.icon && opts.showIcons) {
        // addCircle(context, x, y, 11, null, opts.useTeamColor ? teamColor : 'black');
        // addImage(context, x, y, abilityIconSize, abilityIconSize, abilityObj.icon);

        addAbilityIcon(context, x, y, abilityObj.icon, side, opts.useTeamColor);
      }
    } else if (abilityObj.tool === 'line') {
      // addLine(context, [xCoord, yCoord]);
      return;
    } else if (abilityObj.icon) {
      // addCircle(context, x, y, 14, null, opts.useTeamColor ? teamColor : 'black');
      // addImage(context, x, y, abilityIconSize, abilityIconSize, abilityObj.icon);
      addAbilityIcon(context, x, y, abilityObj.icon, side, opts.useTeamColor);
    }
  }

  return {
    tool: 'agent',
    item,
    center: [x, y],
    agent,
    side
  }
}

export function addAbilityIcon(context, x, y, icon, side, useTeamColor) {
  const teamColor = side === 'a' ? colorAttackers : colorDefenders;
  addCircle(context, x, y, 11, null, useTeamColor ? teamColor : 'black');
  addImage(context, x, y, abilityIconSize, abilityIconSize, icon);
}

export function addAgentLineItem(ability, context, start, end, agent, side, opts) {
  const teamColor = side === 'a' ? colorAttackers : colorDefenders;
  addLine(context, start, end, ability.width, opts.useTeamColor ? teamColor : ability.color, ability.length, opts.showIcons ? ability.icon : null);
  return {
    tool: 'agent',
    item: ability.name,
    abilityTool: 'line',
    agent,
    start,
    end,
    side
  }
}

export function addAnchorPoint(context, x, y, color) {
  addCircle(context, x, y, 3, null, null, color);
  addCircle(context, x, y, 6, null, null, color);
}


