import React, {useCallback, useEffect, useRef, useState} from 'react';
import {ActionButton, ActionGroup, Checkbox, Content, defaultTheme,
  Dialog, DialogTrigger, Divider, Flex, Heading, Image, Item, Provider, Text, TextField, View} from '@adobe/react-spectrum';
import Color from './Color';
import Agent from './Agent';
import {agentLookup} from '../agents';
import {
  addAgentItem,
  addAgentLineItem, addAnchorPoint,
  addArrow,
  addCircle, addLine, addPath, addPoint,
  addSpike,
  addText
} from './canvas/canvasFunctions';
import {ValostratorContextProvider} from './ValostratorContext';
import History from './History';
import {canvasX, canvasY, defaultPalette, sizing} from '../constants';
import Properties from '@spectrum-icons/workflow/Properties';
import Draw from '@spectrum-icons/workflow/Draw';
import Circle from '@spectrum-icons/workflow/Circle';
import Line from '@spectrum-icons/workflow/Line';
import User from '@spectrum-icons/workflow/User';
import ArrowUpRight from '@spectrum-icons/workflow/ArrowUpRight';
import TextIcon from '@spectrum-icons/workflow/Text';
import Alert from '@spectrum-icons/workflow/Alert';


const mapMap = {
  ascent: require('../img/maps/ascent.jpg'),
  bind: require('../img/maps/bind.jpg'),
  haven: require('../img/maps/haven.jpg'),
  split: require('../img/maps/split.jpg'),
};

function getAbility(agent, abilityName) {
  const agentObj = agentLookup[agent];
  return agentObj.abilities.find(a => a.name === abilityName);
}

function getSize(tool, size) {
  return (sizing[tool] || sizing.default)[size];
}

export default function Valostrator() {
  const ref = useRef();
  const textRef = useRef();

  const [size, setSize] = useState('M');
  const [color, setColor] = useState(defaultPalette[0]);
  const [tool, setTool] = useState('circle');
  const [map, setMap] = useState('ascent');
  const [history, setHistory] = useState([]);
  const [agent, setAgent] = useState(Object.keys(agentLookup)[0]);
  const [side, setSide] = useState('a');
  const [agentItem, setAgentItem] = useState('icon');
  const [acceptText, setAcceptingText] = useState(null);  // swithc to start point
  const [useTeamColor, setUseTeamColor] = useState(false);
  const [showIcons, setShowIcons] = useState(false);
  const [startPoint, setStartPoint] = useState(null);
  const [penDown, setPenDown] = useState(false);

  // const handleKeyboard = evt => {
  //   console.log('pressed', evt.key)
  //   switch(evt.key) {
  //     case 'Escape':
  //       if (createArrow) {
  //         setCreateArrow(false);
  //       } else {
  //         onClear();
  //       }
  //       break;
  //     case '1':
  //       setSize(10);
  //       break;
  //     case '2':
  //       setSize(20);
  //       break;
  //     case '3':
  //       setSize(30);
  //       break;
  //     case '4':
  //       setSize(50);
  //       break;
  //     case 'c':
  //       nextColor();
  //       break;
  //     case 's':
  //       setSide(side === 'a' ? 'd' : 'a');
  //       break;
  //   }
  //   evt.stopPropagation();
  // };

  // const nextColor = useCallback(() => {
  //   console.log('color in cb', color)
  //   let colorIndex = colors.findIndex(function (c) {return  c === color});
  //   console.log('colorindex', colorIndex, color)
  //   colorIndex++;
  //   if (colorIndex > colors.length - 1) {
  //     colorIndex = 0;
  //   }
  //   console.log('setting color to ', colors[colorIndex])
  //   setColor(colors[colorIndex]);
  // }, [color]);

  let linePoints = [];
  const onMouseDown = () => tool === 'pencil' && setPenDown(true);
  const onMouseUp = () => {
    if (!penDown) {
      return;
    };
    penDown && setPenDown(false);
    redrawHistory(history, {useTeamColor, showIcons});

    const canvas = ref.current;
    const ctx = canvas.getContext('2d');
    const historyEvent = addPath(ctx, linePoints, getSize('pencil', size), color);
    setHistory([historyEvent, ...history]);
  }

  const onMouseMove = (evt) => {
    if (!penDown) {
      return;
    }

    const canvas = ref.current;
    const rect = canvas.getBoundingClientRect();
    const ctx = canvas.getContext('2d');
    if (linePoints.length) {
      const lastPoint = linePoints[linePoints.length - 1];
      addLine(ctx, lastPoint, [evt.clientX - rect.x, evt.clientY - rect.y], getSize('pencil', size), color);
    }
    linePoints.push([evt.clientX - rect.x, evt.clientY - rect.y]);
    console.log(linePoints.length)
  }
  // useEffect(() => {
  //   document.addEventListener('keydown', handleKeyboard);
  //   return () => {
  //     document.removeEventListener('keydown', handleKeyboard);
  //   };
  // }, [color, side])

  const onClick = evt => {
    if (!ref || !ref.current) {
      return;
    }

    const canvas = ref.current;
    // drawTool(canvas);

    const rect = canvas.getBoundingClientRect();
    const xCoord = evt.clientX - rect.x;
    const yCoord = evt.clientY - rect.y;
    console.log(`click @ (${xCoord}, ${yCoord})`);

    const ctx = canvas.getContext('2d');

    let historyEvent;
    switch (tool) {
      case 'arrow':
        if (!startPoint) {
          setStartPoint([xCoord, yCoord]);
          addAnchorPoint(ctx, xCoord, yCoord, color);
          return;
        }
        redrawHistory(history, {useTeamColor, showIcons});
        historyEvent = addArrow(ctx, startPoint[0], startPoint[1], xCoord, yCoord, color);
        setStartPoint(null);
        break;
      case 'agent':
        // todo make this more optimized? (not looking for ability twice)
        const agentObj = agentLookup[agent];
        if (agentItem !== 'icon') {
          // const abilityObj = agentObj.abilities.find(a => a.name === agentItem);
          const abilityObj = getAbility(agent, agentItem);
          if (abilityObj.tool === 'line') {
            if (!startPoint) {
              setStartPoint([xCoord, yCoord]);
              addAnchorPoint(ctx, xCoord, yCoord, abilityObj.color);
              return;
            } else {
              redrawHistory(history, {showIcons, useTeamColor});
              historyEvent = addAgentLineItem(abilityObj, ctx, startPoint, [xCoord, yCoord], agent, side, {useTeamColor, showIcons});
              setStartPoint(null);
            }
          }
        }
        if (!historyEvent) {
          historyEvent = addAgentItem(agentItem, ctx, xCoord, yCoord, agent, side, {useTeamColor, showIcons});
        }
        break;
      case 'spike':
        historyEvent = addSpike(ctx, xCoord, yCoord);
        break;
      case 'text':
        if (textRef && textRef.current) {
          textRef.current.focus();
        }
        addAnchorPoint(ctx, xCoord, yCoord, color);
        setStartPoint([xCoord, yCoord]);
        break;
      case 'circle':
        historyEvent = addCircle(ctx, xCoord, yCoord, getSize(tool, size), '', '', color);
    }

    // add to front
    if (historyEvent) {
      setHistory([historyEvent, ...history]);
    }
  }

  const clearCanvas = () => {
    if (ref && ref.current) {
      const ctx = ref.current.getContext('2d');
      ctx.clearRect(0, 0, canvasX, canvasY);
    }
  }

  const redrawHistory = (history, {showIcons, useTeamColor}) => {
    console.log('redraw', history)
    clearCanvas();
    const ctx = ref.current.getContext('2d');
    history.map(evt => {
      switch (evt.tool) {
        case 'arrow':
          addArrow(ctx, evt.start[0], evt.start[1], evt.end[0], evt.end[1], evt.color);
          return;
        case 'circle':
          addCircle(ctx, evt.center[0], evt.center[1], evt.size, evt.text, evt.color, evt.stroke)
          return;
        case 'text':
          addText(ctx, evt.center[0], evt.center[1], evt.size, evt.text, evt.color);
          return;
        case 'agent':
          if (evt.abilityTool === 'line') {
            const abilityObj = getAbility(evt.agent, evt.item);
            addAgentLineItem(abilityObj, ctx, evt.start, evt.end, evt.agent, evt.side, {useTeamColor, showIcons})
          } else {
            addAgentItem(evt.item, ctx, evt.center[0], evt.center[1], evt.agent, evt.side, {useTeamColor, showIcons});
          }
          return;
        case 'spike':
          addSpike(ctx, evt.center[0], evt[1]);
          return;
        case 'pencil':
          addPath(ctx, evt.path, evt.size, evt.color);
          return;
      }
    });
  };

  const onClear = () => {
    clearCanvas();
    setHistory([]);
  }

  const onSizeChange = selectedKey => setSize(selectedKey);
  const onColorChange = selectedKey => setColor(selectedKey);
  const onMapChange = selectedKey => setMap(selectedKey);
  const onToolChange = selectedKey => {
    setTool(selectedKey);
    if (tool === 'agent') {
      setAgentItem('icon');
    }
  }
  const onSideChange = selectedKey => setSide(selectedKey);
  const onAbilitySelect = selectedKey => setAgentItem(selectedKey);
  const onAgentChange = selectedKey => {
    if (selectedKey === agent) {
      return;
    }
    setAgent(selectedKey);
    setAgentItem('icon');
  };
  const onUseColorChange = checked => {
    setUseTeamColor(checked);
    redrawHistory(history, {showIcons, useTeamColor: checked});
  };
  const onShowIconChange = checked => {
    setShowIcons(checked);
    redrawHistory(history, {showIcons: checked, useTeamColor});
  };

  const onTextInput = evt => {
    console.log('evtkeycode', evt.keyCode)
    if (evt.keyCode === 13) {
      // setAcceptingText(null);
      setStartPoint(null);
      console.log('submitting', evt.target.value)
      redrawHistory(history, {showIcons, useTeamColor});
      const ctx = ref.current.getContext('2d');
      const historyEvent = addText(ctx, ...startPoint, getSize(tool, size), evt.target.value, color);
      setHistory([historyEvent, ...history]);
      evt.target.value = '';
    }
  }

  const removeHistoryEvent = removeIndex => {
    const newHistory = [...history];
    newHistory.splice(removeIndex, 1);
    setHistory(newHistory);
    redrawHistory(newHistory, {showIcons, useTeamColor});
  }

  const onHistoryChange = selectedKey => {
    // TODO this should be the history replay feature
    // let newCursor = historyCursor || history.length - 1;
    // newCursor = selectedKey === 'back' ? Math.max(0, newCursor - 1) : Math.min(newCursor + 1, history.length);
    // setHistoryCursor(newCursor);
    let newHistory = [...history];
    newHistory.shift();
    setHistory(newHistory);
    redrawHistory(newHistory, {showIcons, useTeamColor});
  }

  const onHistoryClear = () => {
    clearCanvas();
    setHistory([]);
  }

  return (
    <Provider theme={defaultTheme} position="sticky" top={0} bottom={0} width="100%">
      <ValostratorContextProvider {...{
        history,
        onHistoryChange,
        onHistoryClear,
        removeHistoryEvent
      }}>
        <View padding={8}>
          <Flex direction="row">
            <View>
              <Flex gap={20} marginBottom={8}>
                <ActionGroup onAction={onMapChange} selectionMode="single" selectedKeys={[map]} density="compact">
                  <Item key="ascent">Ascent</Item>
                  <Item key="bind">Bind</Item>
                  <Item key="haven">Haven</Item>
                  <Item key="split">Split</Item>
                </ActionGroup>
              </Flex>
              <Flex gap={20} marginBottom={8}>
                <ActionGroup
                  isEmphasized={startPoint}
                  onAction={onToolChange} selectionMode="single" selectedKeys={[tool]} density="compact">
                  <Item key="pencil">
                    <Draw />
                    <Text>Pencil</Text>
                  </Item>
                  <Item key="circle">
                    <Circle />
                    <Text>Circle</Text>
                  </Item>
                  <Item key="arrow">
                    <ArrowUpRight />
                    <Text>Arrow</Text>
                  </Item>
                  <Item key="text">
                    <TextIcon />
                    <Text>Text</Text>
                  </Item>
                  <Item key="agent">
                    <User />
                    <Text>Agent</Text>
                  </Item>
                  <Item key="spike">
                    <Alert />
                    <Text>Spike</Text>
                  </Item>
                </ActionGroup>
              </Flex>
              {
                tool === 'agent' && (
                  <>
                    <Flex gap={20} marginBottom={8}>
                      <ActionGroup onAction={onAgentChange} selectionMode="single" selectedKeys={[agent]} isQuiet>
                        {
                          Object.keys(agentLookup).map(agent => (
                            <Item key={agent} textValue={agent}>
                              <Agent agent={agent} icon={agentLookup[agent].icon} />
                            </Item>
                          ))
                        }
                      </ActionGroup>
                      <ActionGroup onAction={onSideChange} selectionMode="single" selectedKeys={[side]} density="compact">
                        <Item key="a">Attackers</Item>
                        <Item key="d">Defenders</Item>
                      </ActionGroup>
                    </Flex>
                    <Flex gap={20} marginBottom={8}>
                      <ActionGroup onAction={onAbilitySelect} selectionMode="single" selectedKeys={[agentItem]} flexGrow={1}>
                        <Item key="icon">Agent Icon</Item>
                        {
                          agentLookup[agent].abilities.map(ability => (
                            <Item key={ability.name}>{ability.name}</Item>
                          ))
                        }
                      </ActionGroup>
                      <DialogTrigger type="popover" hideArrow={true} placement="top right">
                        <ActionButton>
                          <Properties />
                        </ActionButton>
                        <Dialog>
                          <Heading>View Controls</Heading>
                          <Divider />
                          <Content>
                            <Checkbox isSelected={useTeamColor} onChange={onUseColorChange}>Use team color for abilities</Checkbox>
                            <Checkbox isSelected={showIcons} onChange={onShowIconChange}>Show icons on all abilities</Checkbox>
                          </Content>
                        </Dialog>
                      </DialogTrigger>
                    </Flex>
                  </>
                )
              }
              {
                ['arrow', 'circle', 'pencil', 'text'].includes(tool) && (
                  <Flex gap={20} marginBottom={8}>
                    <View>
                    <ActionGroup onAction={onSizeChange} selectionMode="single" selectedKeys={[size.toString()]} density="compact">
                      <Item key="S">S</Item>
                      <Item key="M">M</Item>
                      <Item key="L">L</Item>
                      <Item key="XL">XL</Item>
                    </ActionGroup>
                    </View>
                    <ActionGroup onAction={onColorChange} selectionMode="single" selectedKeys={[color]} density="compact">
                      {
                        defaultPalette.map(color => (
                          <Item key={color} textValue={color}>
                            <Color color={color} />
                          </Item>
                        ))
                      }
                    </ActionGroup>
                    {(tool === 'text') && <TextField ref={textRef} onKeyDown={onTextInput} isDisabled={!startPoint} />}
                  </Flex>
                )
              }
              <div style={{display: 'inline-block', width: canvasX + 'px', height: canvasY + 'px',}}>
                <img src={mapMap[map]} style={{position: 'absolute', width: canvasX + 'px', height: canvasY + 'px', zIndex: 1 }} />
                <canvas
                  ref={ref}
                  onClick={onClick}
                  onMouseDown={onMouseDown}
                  onMouseUp={onMouseUp}
                  onMouseMove={onMouseMove}
                  width={canvasX}
                  height={canvasY}
                  style={{
                    position: 'absolute',
                    zIndex: 100
                  }} />
              </div>
            </View>
            <History />
          </Flex>
        </View>
      </ValostratorContextProvider>
    </Provider>
  )
}
