import React, {createContext} from 'react';


export const ValostratorContext = createContext();

export function ValostratorContextProvider(props) {
  const {
    children,
    ...otherProps
  } = props
  return (
    <ValostratorContext.Provider value={{...otherProps}}>
      {children}
    </ValostratorContext.Provider>
  )
}
